import React from 'react';
import Slider from "react-slick";
import './Homesection.scss';
import { CgArrowLeftR, CgArrowRightR } from 'react-icons/cg';
import 'slick-carousel/slick/slick.css';
import styled from 'styled-components';

const Article = styled.div`
    position: relative; z-index: 1;
    .slick-arrow {
        width: 60px; height: 60px;
        position: absolute; top: 50%;
        transform: translateY(-50%);
        color: #fff;
        padding: 10px;
        z-index: 99;
        &.slick-prev {left: 100px;}
        &.slick-next {right: 100px;}
    }
    .slick-dots {
        position: absolute; bottom: 10%; left: 50%;
        transform: translate(-50%);
        li {
            display: inline-block;
            &+li {padding-left:10px;}
            button {
                width: 20px; height: 20px;
                border-radius: 50%;
                background: #fff;
                overflow: hidden; text-indent: -999999px;
            }
            &.slick-active {
                button {background:red;}
            }
        }
    }
    .slide {
        position: relative;
        // height: 100vh !important;
        width: 100%;
        .img-box {
            width: 100%;
            img {
                width: 100%;
            }
        }
        .text {
            position: absolute; left: 50%; top: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            font-size: 3vw;
            text-align: center;
            button {
                padding: 5px 10px; margin-top: 20px;
                border-radius: 10px;
                background: #fff;
                box-shadow: 0px 0px 5px #ccc;
                &:hover {
                    background: #ccc;
                    font-weight: bold;
                    box-shadow: 0px 0px 5px #aaa;
                }
            }
        }
    };
`;

const Homesection = () => {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed : 3500,
        pauseOnHover : false,
        pauseOnDotsHover : true,
        pauseOnFocus : true,
        draggable : true,
        prevArrow: <CgArrowLeftR />,
        nextArrow: <CgArrowRightR />
    };

    return (
        <div className="homesecWrap">
            <Article className="article1">
                <Slider {...settings}>
                    <div className="slide slide1">
                        <div className="img-box">
                            <img src="images/air-main1.jpg" alt="" />
                        </div>
                        <div className="text">
                            <p>이제, 여행은 가까운 곳에서</p>    
                            <button>근처의 숙소 둘러보기</button>
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="img-box">
                            <img src="images/air-main2.jpg" alt="" />
                        </div>
                        <div className="text">
                            <p>이제, 여행은 가까운 곳에서</p>    
                            <button>근처의 숙소 둘러보기</button>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="img-box">
                            <img src="images/air-main3.jpg" alt="" />
                        </div>
                        <div className="text">
                            <p>이제, 여행은 가까운 곳에서</p>    
                            <button>근처의 숙소 둘러보기</button>
                        </div>
                    </div>
                    <div className="slide slide4">
                        <div className="img-box">
                            <img src="images/air-main4.jpg" alt="" />
                        </div>
                        <div className="text">
                            <p>이제, 여행은 가까운 곳에서</p>    
                            <button>근처의 숙소 둘러보기</button>
                        </div>
                    </div>
                </Slider>
            </Article>

            <article className="article2">
                <div className="row">
                    <ul>
                        <li>
                            <a href="#">
                                <img src="images/country1.jpg" alt="" />
                                <div>
                                    <strong>대한민국</strong>
                                    <p>경복궁</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/country2.jpg" alt="" />
                                <div>
                                    <strong>영국</strong>
                                    <p>Saint Paul's Cathedral</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/country3.jpg" alt="" />
                                <div>
                                    <strong>러시아</strong>
                                    <p>Национальный исторический музей</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/country4.jpg" alt="" />
                                <div>
                                    <strong>인도</strong>
                                    <p>ताज महल</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/country5.jpg" alt="" />
                                <div>
                                    <strong>이집트</strong>
                                    <p>أبو الهول</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src="images/country6.jpg" alt="" />
                                <div>
                                    <strong>일본</strong>
                                    <p>中銀カプセルタワービル</p>
                                </div>
                            </a>
                        </li>
                    </ul>
                    <div className="lifeStyle">
                        <h2>어디에서나, 여행은 살아보는거야!</h2>
                        <ul>
                            <li>
                                <img src="images/house1.jpg" alt="" />
                                <strong>집 전체</strong>
                            </li>
                            <li>
                                <img src="images/house2.jpg" alt="" />
                                <strong>독특한 공간</strong>
                            </li>
                            <li>
                                <img src="images/house3.jpg" alt="" />
                                <strong>반려동물 환영</strong>
                            </li>
                        </ul>
                    </div>
                </div>
            </article>

            <article className="article3">
                <div className="row">
                    <div>
                        <img src="images/study1.jpg" alt="" />
                        <p>하루를 마무리하며 좋은 추억 만들기</p>
                    </div>
                    <div className="right">
                        <div className="rightFst">
                            <div>
                                <img src="images/study2.jpg" alt="" />
                                <p>여성화가에 대하여 얘기하기</p>
                            </div>
                            <div>
                                <img src="images/study3.jpg" alt="" />
                                <p>뉴욕 마술사의 마술공연 구경하기</p>
                            </div>
                        </div>
                        <div>
                            <img src="images/study4.jpg" alt="" />
                            <p>재밌게 맛있는 디저트 만드는 법 배우기</p>
                        </div>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Homesection;