import React from 'react';
import { ImFacebook } from 'react-icons/im';
import { AiOutlineTwitter } from 'react-icons/ai';
import { FaCameraRetro, FaInstagramSquare } from 'react-icons/fa';
import styled from 'styled-components';

const AllFooter = styled.div`
    background: #f7f7f7;
    .footer-box {
        text-align: center;
        .footer-txt {
            li {
                display: inline-block;
                padding: 15px 0px;
                color: #333;
                strong {
                    margin: 0px 10px;
                    font-weight: normal;
                }
            }
        }
        .footer-icon {
            padding: 0px 0px 20px 0px;
            color: #555;
            li {
                font-size: 25px;
                display: inline-block;
                margin: 0px 20px;
            }
        }
        .copy {
            padding-bottom: 10px;
            color: #aaa;
        }
    }
`;

const Footer = () => {
    return (
        <AllFooter>
            <div className="footer-box">
                <ul className="footer-txt">
                    <li>개인정보 처리방침</li>
                    <li><strong>|</strong>이용약관</li>
                    <li><strong>|</strong>사이트맵</li>
                    <li><strong>|</strong>한국의 변경된 환불 정책</li>
                    <li><strong>|</strong>회사 세부정보</li>
                </ul>
                <ul className="footer-icon">
                    <li><ImFacebook /></li>
                    <li><AiOutlineTwitter /></li>
                    <li><FaCameraRetro /></li>
                    <li><FaInstagramSquare /></li>
                </ul>
                <div className="copy">© 2020 Airbnb, Inc. All rights reserved</div>
            </div>
        </AllFooter>
    );
};

export default Footer;