import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { AiOutlineReload } from 'react-icons/ai';


const NewsSectionBlock = styled.div`
    // border-top: 80px solid #999;
    .row {
        padding-top: 80px;
        display: flex; flex-wrap: wrap;
    }
    .loadWrap {
        min-height: 800px; line-height: 800px;
        text-align: center;
        .loadicon {
            font-size:100px;
            animation: newsLoading 2s infinite linear;
        }
        @keyframes newsLoading {
            from {transform: rotate(0deg);}
            to {transform: rotate(1turn);}
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async () => {
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=025e80112b714619a6ae4b2bd7ff22c2`);
        // console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }
    useEffect(
        () => {
            getNews()
        }, [category]
    )

    if (!loading) {
        return (
            <NewsSectionBlock>
                <div className="loadWrap">
                    <AiOutlineReload className="loadicon" />
                </div>
            </NewsSectionBlock>
        )
    } else {
        if (!articles) {
            return <NewsSectionBlock></NewsSectionBlock>;
        } else {
            return (
                <NewsSectionBlock>
                    <div className="row">
                        {
                            articles.map( (article) => (<NewsItem key={article.url} article={article} />) )
                        }
                    </div>
                </NewsSectionBlock>
            );
        }
    }

    

};

export default NewsSection;